import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AddPackageModuleComponent } from './add-package-module.component';

const routes: Routes = [{ path: '', component: AddPackageModuleComponent }];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AddPackageModuleRoutingModule { }



