import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { AddPackageModuleRoutingModule } from './add-package-module-routing.module';
import { AddPackageModuleComponent } from './add-package-module.component';
import { ReactiveFormsModule } from '@angular/forms';
import { CoreModule } from 'core/core.module';
import { CommonComponentsModule } from 'common/common.module';


@NgModule({
  declarations: [AddPackageModuleComponent],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    CoreModule,
    AddPackageModuleRoutingModule,
    CommonComponentsModule
  ],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ]

})
export class AddPackageModuleModule { }



