import { UserLogin } from 'core/models/userloginresponse';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Constants } from 'core/constants';
import { HttpUserUtilsService } from 'core/http_wrapper/http-user-utils.service';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { HttpSuccesFailureResponse } from 'core/http_wrapper/http_wrapper_response';
import { ClassDetail } from 'core/models/classListResponse';
import { LocalStorageService } from 'core/service/local-storage.service';
import { ActivatedRoute, Router } from '@angular/router';
import { PakagesData } from 'core/models/packagesListResponse';
import { element } from 'protractor';
import { CustomData } from 'core/autocomplete/custom_data';

@Component({
  selector: 'app-add-package-module',
  templateUrl: './add-package-module.component.html',
  styleUrls: ['./add-package-module.component.css']
})
export class AddPackageModuleComponent implements OnInit,HttpSuccesFailureResponse {
  allPackageDetails:PakagesData[] = [];
  allBuyablePackages:PakagesData[] = [];

  currentPage: number = 0;
  addPackageForm: FormGroup;
  userData:UserLogin;
  selectedPackageData:any;
  userIdAddPkg:number=0;
  isMasterGym:boolean=false;
  public customData: CustomData;
  public userSearch:any;

  constructor(private route: ActivatedRoute,private user_http: HttpUserUtilsService, private http_userauth: HttpUserAuthUtilsService,public formBuilder: FormBuilder,private router: Router) {
    this.customData = new CustomData(this.http_userauth);
    this.customData.isFromAddPackage=true;
   }
  onSuccess(type: any, responsedata: any) {

    if(type==Constants.addPackageAPI){
      alert("Package successfully assigned");
      setTimeout(() => {
        this.router.navigate(['/gym/transaction']);
      }, 500);
    }
    
    if(type==Constants.TrainerUpcomingClassesApi){
      this.allPackageDetails = responsedata.data;
    }
    if(type == Constants.findUserByEmailApi){
      this.addPackageForm.patchValue({errorMessage:''});
      this.addPackageForm.updateValueAndValidity();
       this.userData = responsedata; 
       this.getBuyablePackages(this.userData);
       this.userIdAddPkg=this.userData.data.id;
      this.addPackageForm.patchValue({name:this.userData.data.firstName});
      try{
        if(!this.isMasterGym){
          this.userData.data.userPackageDetailDtoList=this.userData.data.userPackageDetailDtoList.filter((element) => {
            if(element.gymId==LocalStorageService.getUserId()){
              return true;
            }
       
          });
        }
      }catch(e){

      }
      
      
      if(this.userData.data.userPackageDetailDtoList&&this.userData.data.userPackageDetailDtoList.length>0){
       let packageDetail =  this.userData.data.userPackageDetailDtoList[0].balanceClass+" Of "+this.userData.data.userPackageDetailDtoList[0].totalNumberOfClass+" remaining";
        this.addPackageForm.patchValue({package:packageDetail});
        this.addPackageForm.patchValue({packageBalance:this.userData.data.userPackageDetailDtoList[0].balanceClass});

      }else{
        this.addPackageForm.patchValue({package:"NA"});

      }
      this.addPackageForm.updateValueAndValidity();
     



    }
    if(type==Constants.JoinClassApi){
      this.router.navigate(['/gym/bookings']); 
    }
    if (type == Constants.getPackageListApi) { 
      this.allPackageDetails = responsedata.data.filter((element) => {
        
        return element.packageType != "AUTOJOINWITHPACKAGE";
      
    });
      this.allBuyablePackages=this.allPackageDetails; 
  
      }
  }
  onFailure(type: any, response: any) {
    if(type==Constants.addPackageAPI){
      this.addPackageForm.patchValue({errorMessage:'Error in assigning package to the customer'});
    }
    if(type==Constants.findUserByEmailApi){
      this.addPackageForm.patchValue({errorMessage:'User email does not exist.'});
      this.addPackageForm.updateValueAndValidity(); 
    }
    if(type==Constants.TrainerUpcomingClassesApi){
      
    }  
    if(type==Constants.JoinClassApi){
      if (response.statusCode == Constants.gymVisitLimitExeeded ) {
        this.addPackageForm.patchValue({errorMessage:'Customer have exceeded the number of visits.'});
    this.addPackageForm.updateValueAndValidity();
    
      }else if (response.statusCode == Constants.paymentRequiredError) { 
        if(this.userData.data.userPackageDetailDtoList&&this.userData.data.userPackageDetailDtoList.length>0){
          this.addPackageForm.patchValue({errorMessage:'Customers package expiring before the class'});
    this.addPackageForm.updateValueAndValidity(); 
        }else{
          this.addPackageForm.patchValue({errorMessage:'Customer do not have any active plan'});
          this.addPackageForm.updateValueAndValidity(); 

        }
    }
    else if (JSON.parse(response._body).statusCode === '4XX'){
      this.addPackageForm.patchValue({errorMessage:'Error in addding booking try after some time.'});
      this.addPackageForm.updateValueAndValidity();
      return;

    }
    else if (JSON.parse(response._body).statusCode === Constants.paymentRequiredError){
      if(this.userData.data.userPackageDetailDtoList&&this.userData.data.userPackageDetailDtoList.length>0){
        this.addPackageForm.patchValue({errorMessage:'Customers package expiring before the class'});
  this.addPackageForm.updateValueAndValidity(); 
      }else{
        this.addPackageForm.patchValue({errorMessage:'Customer do not have any active plan'});
        this.addPackageForm.updateValueAndValidity(); 

      }
    }
    else if (JSON.parse(response._body).statusCode === Constants.gymVisitLimitExeeded){
      this.addPackageForm.patchValue({errorMessage:'Customer have exceeded the number of visits.'});
    this.addPackageForm.updateValueAndValidity();
    }
    }
  
  }

  ngOnInit(): void {
    this.isMasterGym = LocalStorageService.isMasterGym();
    this.addPackageForm = this.formBuilder.group({
      email: ['', [Validators.required]],
      name: ['', [Validators.maxLength(600), Validators.required]],
      package: ['', [Validators.required]],
      packageTotal:['0',Validators.required],
      packageTotalUnlimited:['0'],

      packageTotalCredits:['0',Validators.required],
      packageId:[''],
      amount:[''],
      packageCategoryType:[''],
      selectedKid:[''],

      errorMessage:['']

     });
    if(this.isMasterGym){
      this.http_userauth.getAllMembershipPackages(this,true,-1);

    }else{
      this.http_userauth.getAllMembershipPackages(this,true,LocalStorageService.getUserId());

    }

    this.route.queryParams.subscribe(params => {
      if(params['email']){
        this.addPackageForm.patchValue({email:params['email']});
        this.addPackageForm.updateValueAndValidity();
        this.findUser();
      }
      
      });
  }
  onUserSelected(event:any){

    this.http_userauth.getUserProfileByEmail(event.originalObject.email,this,true)
    }
  findUser(){
    this.allBuyablePackages=[];
    
    this.addPackageForm.patchValue({packageId:''});
    this.addPackageForm.patchValue({packageTotal:''});
    this.addPackageForm.patchValue({packageTotalUnlimited:''});
    this.addPackageForm.patchValue({packageCategoryType:''});

    

    
    this.addPackageForm.patchValue({packageTotalCredits:''})
    this.addPackageForm.patchValue({selectedKid:''})

    this.addPackageForm.controls['selectedKid'].setValidators([]);
    this.addPackageForm.controls['selectedKid'].updateValueAndValidity();
    this.addPackageForm.updateValueAndValidity(); 

    if(this.addPackageForm.value.email){
      this.http_userauth.getUserProfileByEmail(this.addPackageForm.value.email,this,true);
    }
  }

  getBuyablePackages(userData){

    console.log(userData.data.userPackageDetailDtoList)
    console.log(this.allPackageDetails)
   if(userData.data.userPackageDetailDtoList){
    let userPackageList=userData.data.userPackageDetailDtoList.filter((element) => {
      return element.balanceClass>0;
    }); 
    
    if(userPackageList && userPackageList.length>0){
      let packages=[]
      userPackageList.forEach(element => {
       packages.push(element.packageId)
      });
      this.allBuyablePackages = this.allPackageDetails.filter(function(obj) {
            return packages.indexOf(obj.id)==-1;
        
    });
    }else{
      this.allBuyablePackages = this.allPackageDetails;
  
    }
   
    
    
   
   }
   else{
    this.allBuyablePackages = this.allPackageDetails;
   }

  }

  selectPackage(event){
    
    this.allPackageDetails.forEach(element => {
        console.log(element);
    });
    if (event.target.value) {
      this.addPackageForm.patchValue({sessionId:event.target.value});
      this.getSelectedPkgData(event.target.value);
    } 
  }

  getSelectedPkgData(packageId:any){
    this.allPackageDetails.forEach(element => {
      if(element.id == packageId){
        this.selectedPackageData=element;
        
      }
  });
  if(this.selectedPackageData.noOfClass==9999999){
    this.addPackageForm.patchValue({packageTotalUnlimited:"Unlimited Classes"});

  }
  
  this.addPackageForm.patchValue({packageTotal:this.selectedPackageData.noOfClass});
  this.addPackageForm.patchValue({amount:this.selectedPackageData.cost});

  this.addPackageForm.patchValue({packageTotalCredits:this.selectedPackageData.credits})
  this.addPackageForm.patchValue({packageCategoryType:this.selectedPackageData.packageCategoryType});
   if(this.selectedPackageData.packageCategoryType=="CHILD"){
    this.addPackageForm.controls['selectedKid'].setValidators([Validators.required]);
    if(this.userData.data.userKidsList&&this.userData.data.userKidsList.length>0){
        if(this.userData.data.userKidsList.length==1){
          this.addPackageForm.patchValue({selectedKid:this.userData.data.userKidsList[0].id});
    this.addPackageForm.updateValueAndValidity(); 
        }
    }else{
      this.addPackageForm.patchValue({errorMessage:'User have no child to assign this package'});
    this.addPackageForm.updateValueAndValidity();
    }
    this.addPackageForm.controls['selectedKid'].updateValueAndValidity();

   }else{
    this.addPackageForm.controls['selectedKid'].setValidators([]);
    this.addPackageForm.controls['selectedKid'].updateValueAndValidity();
    this.addPackageForm.patchValue({selectedKid:''});
    this.addPackageForm.updateValueAndValidity();

   }
  }

  addPackage(){
   
    if(this.addPackageForm.valid){
      this.addPackageForm.patchValue({errorMessage:''});
      this.addPackageForm.updateValueAndValidity();
      if(this.isMasterGym){
        this.http_userauth.addPackageCustomer(this.userIdAddPkg,this.selectedPackageData.id,this.addPackageForm.value.amount,this.addPackageForm.value.selectedKid,this,true);

      }else{
        this.http_userauth.addPackageCustomer(this.userIdAddPkg,this.selectedPackageData.id,this.addPackageForm.value.amount,this.addPackageForm.value.selectedKid,this,true,LocalStorageService.getUserId());

      }
    }else{
    this.markFormGroupTouched(this.addPackageForm);
    }
  }


  private markFormGroupTouched(formGroup: FormGroup) {
    try {
      (<any>Object).values(formGroup.controls).forEach(control => {
        control.markAsTouched();
        if (control.controls) {
          control.controls.forEach(c => this.markFormGroupTouched(c));
        }
      });
    } catch (ex) {
      // Code to handle exception
    }


  }
}



